            line-height: 0.18rem;
            float: left;
            margin-top: 0;
            color: #666666;
            font-size: 0.18rem;
          }
        }
      }
    }
  }
  .pro_buy {
    width: 100%;
    height: 0.5rem;
    padding: 0 0.15rem;
    position: fixed;
    bottom: 0;
    background: white;
    div {
      width: 2.3rem;
      height: 0.36rem;
      margin: 0.07rem 0;
      span {
        width: 50%;
        height: 100%;
        float: left;
        font-size: 0.18rem;
        line-height: 0.36rem;
        text-align: center;
      }
      span:nth-of-type(1) {
        background: #FFE7E8;
        border-radius: 0.18rem 0 0 0.18rem;
        color: #FF5959;
      }
      span:nth-of-type(2) {
        background: #FF5959;
        border-radius: 0 0.18rem 0.18rem 0;
        color: #FFFFFF;
      }
    }
    img {
      width: 0.55rem;
      height: 0.6rem;
      float: left;
      position: absolute;
      right: 0.25rem;
      bottom: 0.1rem;
    }
  }
}
.copyTel {
  width: 3.60rem;
  height: 0.88rem;
  border-radius: 10px!important;
  background: white;
  margin: auto;
  padding: 0.24rem 0.15rem;
  span:nth-of-type(1) {
