    line-height: 0.4rem;
    width: 2.2rem;
    height: 0.4rem;
    font-size: 0.3rem;
    font-weight: bold;
    text-align: center;
    color: #5b5b5b;
  }
  span:nth-of-type(2) {
    float: right;
    width: 1rem;
    height: 0.4rem;
    text-align: center;
    line-height: 0.4rem;
    font-size: 0.2rem;
    background: #04e5af;
